body {
    background-color: var(--clr-light);
    font-family: var(--ff-primary);
}

.timelineRow__body {
    display: flex;
    overflow-x: auto;

}

/* Timeline Row Item */

.timelineRow__item {
    display: flex;
    padding: 0.5rem 2rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
    position: relative;
}

.timelineRow__item--dates {
    display: flex;
    font-size: 0.7rem;
}

.timelineRow__item--start {
    color: var(--clr-confirm);
}

.timelineRow__item--end {
    color: var(--clr-warning);
}

.timelineRow__item--date + .timelineRow__item--date::before {
    content: "/";
    color: black;
    padding: 0 4px;
}

.timelineRow__item--edit {
    position: absolute;
    top: 0;
    right: 0;
    margin: 3px 3px 0 0;
    font-size: 0.8rem;
}

/* edit button */

.edit__btn--body {
    margin: 1rem;
}

.btn {
    width: 75px;
    text-align: center;
    padding: 0.4rem;
    color: var(--clr-light);
}

.btn:hover {
    cursor: pointer;
}

.edit__btn--edit {
    background-color: var(--clr-confirm);
}

.edit__btn--edit:hover {
    background-color: var(--clr-light);
    outline: 2px solid var(--clr-confirm);
    color: var(--clr-confirm);
}

.edit__btn--editting {
    display: flex;
}

.edit__btn--save {
    background-color: var(--clr-confirm);
}

.edit__btn--save:hover {
    background-color: var(--clr-light);
    outline: 2px solid var(--clr-confirm);
    color: var(--clr-confirm);
}

.edit__btn--cancel {
    margin-left: 0.5rem;
    background-color: var(--clr-danger);
}

.edit__btn--cancel:hover {
    background-color: var(--clr-light);
    outline: 2px solid var(--clr-danger);
    color: var(--clr-danger);
}