/* most css in timeline.css */

*, *::before, *::after {
    box-sizing: border-box;
}

:root {
    --ff-primary: "Source Sans Pro", sans-serif;
    --ff-secondary: "Source Code Pro", monospace;
    --fw-reg: 300;
    --fw-bold: 900;
    --clr-main: rgb(225, 73, 132);
    --clr-secondary: rgb(138, 84 ,168);
    --clr-light: rgb(255, 255, 255);
    --clr-confirm: rgb(0, 151, 0);
    --clr-warning: rgba(68, 0, 0, 0.521);
    --clr-danger: rgb(247, 0, 0);
    --fs-h1: 3rem;
    --fs-h2: 2rem;
    --fs-h3: 1.25rem;
    --fs-body: 1rem;
    --bs: 0.25em 0.25em 0.75em rgba(0, 0, 0, 0.25), 0.125em 0.125em 0.25em rgba(0, 0, 0, 0.15);
}

html[data-theme="alt"] {
    --ff-primary: '"Lucida Sans Unicode', sans-serif;
    --ff-secondary: "Lucida Console", monospace;
    --clr-light: rgb(255, 236, 236);
    --clr-dark: rgb(21, 25, 83);
    --clr-accent: rgb(255, 113, 113);
    --clr-dark-lite: rgba(21, 25, 83, 0.7);
    --clr-accent-lite: rgb(255, 113, 113, 0.7);
}